import { Component, Prop, State, h, Host } from '@stencil/core';
import type { TooltipItem } from '../bh-chart-tooltip/bh-chart-tooltip';

export type BarChartType = 'simple' | 'stacked';
export type BarChartOrientation = 'vertical' | 'horizontal';

export interface BarChartSeries {
  name: string;
  values: number[];
  color?: string;
}

export interface BarChartData {
  series: BarChartSeries[];
  categories?: string[];
}

interface BarSegment {
  value: number;
  height: number;
  width: number;
  color: string;
  seriesName?: string;
}

interface ProcessedBar {
  segments: BarSegment[];
  totalValue: number;
  category?: string;
}

const DEFAULT_COLORS = [
  'var(--color-brand-600)',
  'var(--color-orange-500)',
  'var(--color-success-500)',
  'var(--color-purple-600)',
  'var(--color-accent-500)',
  'var(--color-teal-500)',
  'var(--color-lime-500)',
  'var(--color-amber-500)',
];

@Component({
  tag: 'bh-bar-chart',
  styleUrl: 'bh-bar-chart.css',
  shadow: true,
})
export class BhBarChart {
  /**
   * Chart data
   */
  @Prop() data: BarChartData = {
    series: [
      { name: 'Series 1', values: [30, 45, 20, 35, 50, 40, 60, 35, 25, 55, 40], color: '#94a3b8' },
      { name: 'Series 2', values: [20, 25, 40, 30, 35, 45, 25, 30, 40, 20, 50], color: '#2563eb' },
      { name: 'Series 3', values: [25, 15, 35, 20, 15, 20, 30, 25, 30, 35, 25], color: '#7c3aed' },
    ],
  };

  /**
   * Chart type: simple or stacked
   */
  @Prop() type: BarChartType = 'stacked';

  /**
   * Chart orientation: vertical or horizontal
   */
  @Prop() orientation: BarChartOrientation = 'vertical';

  /**
   * Whether to animate the chart
   */
  @Prop() animated: boolean = true;

  /**
   * Maximum height for vertical bars
   */
  @Prop() maxHeight: number = 150;

  /**
   * Maximum width for horizontal bars
   */
  @Prop() maxWidth: number = 200;

  @State() tooltipVisible: boolean = false;
  @State() tooltipX: number = 0;
  @State() tooltipY: number = 0;
  @State() tooltipHeader: string = '';
  @State() tooltipItems: TooltipItem[] = [];
  @State() tooltipTotal: string = '';
  @State() showTooltipTotal: boolean = false;

  private processData(): ProcessedBar[] {
    if (!this.data.series?.length || !this.data.series[0].values.length) {
      return [];
    }

    const bars: ProcessedBar[] = [];

    if (this.type === 'simple') {
      const values = this.data.series[0].values;
      const maxValue = Math.max(...values);

      for (let i = 0; i < values.length; i++) {
        const value = values[i] || 0;
        const segments: BarSegment[] = [];

        if (value > 0) {
          const height = (value / maxValue) * this.maxHeight;
          const width = (value / maxValue) * this.maxWidth;
          segments.push({
            value,
            height,
            width,
            color: DEFAULT_COLORS[i % DEFAULT_COLORS.length],
            seriesName: this.data.categories?.[i] || `Bar ${i + 1}`,
          });
        }

        bars.push({
          segments,
          totalValue: value,
          category: this.data.categories?.[i],
        });
      }
    } else {
      const numBars = this.data.series[0].values.length;
      const maxTotal = Math.max(
        ...Array.from({ length: numBars }, (_, i) =>
          this.data.series.reduce((sum, series) => sum + (series.values[i] || 0), 0)
        )
      );

      for (let barIndex = 0; barIndex < numBars; barIndex++) {
        const segments: BarSegment[] = [];
        let totalValue = 0;

        for (const series of this.data.series) {
          const value = series.values[barIndex] || 0;
          totalValue += value;

          if (value > 0) {
            const height = (value / maxTotal) * this.maxHeight;
            const width = (value / maxTotal) * this.maxWidth;
            segments.push({
              value,
              height,
              width,
              color: series.color || '#64748b',
              seriesName: series.name,
            });
          }
        }

        bars.push({
          segments,
          totalValue,
          category: this.data.categories?.[barIndex],
        });
      }
    }

    return bars;
  }

  private handleColumnHover = (event: MouseEvent, bar: ProcessedBar, columnIndex: number) => {
    this.tooltipVisible = true;
    this.tooltipX = event.clientX;
    this.tooltipY = event.clientY;
    this.tooltipHeader = bar.category || `Column ${columnIndex + 1}`;

    const items: TooltipItem[] = [];
    bar.segments.forEach((segment) => {
      if (segment.seriesName && segment.value > 0) {
        items.push({
          color: segment.color,
          label: segment.seriesName,
          value: segment.value.toString(),
        });
      }
    });

    this.tooltipItems = items;
    this.tooltipTotal = bar.totalValue.toString();
    this.showTooltipTotal = items.length > 1;
  };

  private handleColumnMove = (event: MouseEvent) => {
    if (this.tooltipVisible) {
      this.tooltipX = event.clientX;
      this.tooltipY = event.clientY;
    }
  };

  private handleColumnLeave = () => {
    this.tooltipVisible = false;
  };

  render() {
    const processedData = this.processData();

    return (
      <Host>
        <div
          class={{
            'bar-chart': true,
            [this.type]: true,
            [this.orientation]: true,
            'bar-chart-animated': this.animated,
          }}
        >
          {this.orientation === 'vertical' ? (
            <div class="vertical-container">
              {processedData.map((bar, i) => (
                <div
                  key={i}
                  class="bar-column vertical"
                  onMouseEnter={(e) => this.handleColumnHover(e, bar, i)}
                  onMouseMove={this.handleColumnMove}
                  onMouseLeave={this.handleColumnLeave}
                >
                  {bar.segments.map((segment, segIndex) => (
                    <div
                      key={segIndex}
                      class="bar-segment vertical"
                      style={{
                        height: `${segment.height}px`,
                        backgroundColor: segment.color,
                      }}
                    />
                  ))}
                </div>
              ))}
            </div>
          ) : (
            <div class="horizontal-container">
              {processedData.map((bar, i) => (
                <div
                  key={i}
                  class="bar-row horizontal"
                  onMouseEnter={(e) => this.handleColumnHover(e, bar, i)}
                  onMouseMove={this.handleColumnMove}
                  onMouseLeave={this.handleColumnLeave}
                >
                  {bar.segments.map((segment, segIndex) => (
                    <div
                      key={segIndex}
                      class="bar-segment horizontal"
                      style={{
                        width: `${(segment.value / bar.totalValue) * 100}%`,
                        backgroundColor: segment.color,
                      }}
                    />
                  ))}
                </div>
              ))}
            </div>
          )}
        </div>

        <bh-chart-tooltip
          visible={this.tooltipVisible}
          x={this.tooltipX}
          y={this.tooltipY}
          header={this.tooltipHeader}
          items={this.tooltipItems}
          total={this.tooltipTotal}
          showTotal={this.showTooltipTotal}
        />
      </Host>
    );
  }
}
